import React from "react";
import { Link, Redirect } from "react-router-dom";
import Player from "../../main/Player";
import useQuery from "../../main/useQuery";

function generateRoomId() {
  return btoa(new Date().getTime().toString()).replace(/=+/, "");
}

export default function Welcome() {
  console.log("WELCOME RENDERED");

  const query = useQuery();
  const roomId = query.get("roomId") as string;
  const playerId = query.get("playerId") as Player;

  return (
    <main>
      {roomId && playerId && (
        <Redirect to={`/versus?roomId=${roomId}&playerId=${playerId}`} />
      )}
      <span>Create a game: </span>
      <Link to={`/versus?roomId=${generateRoomId()}&playerId=x`}>New Game</Link>
    </main>
  );
}
